.home-page {
  /* CUSTOMIZE THE CAROUSEL
  -------------------------------------------------- */
  /* Carousel base class */
  /* Since positioning the image, we need to help out the caption */
  /* Declare heights because of positioning of img element */
  /* MARKETING CONTENT
  -------------------------------------------------- */
  /* Center align the text within the three columns below the carousel */
  /* Featurettes
  ------------------------- */
  /* Thin out the marketing headings */
  /* RESPONSIVE CSS
  -------------------------------------------------- */ }
  .home-page .carousel {
    margin-bottom: 4rem; }
  .home-page .carousel-caption {
    z-index: 10;
    bottom: 3rem; }
  .home-page .carousel-item {
    height: 32rem;
    background-color: #777; }
  .home-page .carousel-item > img {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    height: 32rem; }
  .home-page .marketing .col-lg-4 {
    margin-bottom: 1.5rem;
    text-align: center; }
  .home-page .marketing h2 {
    font-weight: normal; }
  .home-page .marketing .col-lg-4 p {
    margin-right: 0.75rem;
    margin-left: 0.75rem; }
  .home-page .featurette-divider {
    margin: 5rem 0;
    /* Space out the Bootstrap <hr> more */ }
  .home-page .featurette-heading {
    font-weight: 300;
    line-height: 1;
    letter-spacing: -.05rem; }
  @media (min-width: 40em) {
    .home-page {
      /* Bump up size of carousel content */ }
      .home-page .carousel-caption p {
        margin-bottom: 1.25rem;
        font-size: 1.25rem;
        line-height: 1.4; }
      .home-page .featurette-heading {
        font-size: 50px; } }
  @media (min-width: 62em) {
    .home-page .featurette-heading {
      margin-top: 7rem; } }



