  color: #f6f6f6; }

body {
  background-color: #1e2124; }

.loading-content {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999999 !important; }
  .loading-content .loading-center {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .loading-content .sk-double-bounce {
    width: 100px;
    height: 100px;
    position: relative;
    margin: auto; }
  .loading-content .sk-double-bounce .sk-child {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #337ab7;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-animation: sk-double-bounce 2.0s infinite ease-in-out;
    animation: sk-double-bounce 2.0s infinite ease-in-out; }
  .loading-content .sk-double-bounce .sk-double-bounce-2 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s; }

@-webkit-keyframes sk-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0); }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes sk-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0); }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

.main-menu {
  background-color: #000; }
  .main-menu li.nav-item {
    color: #FBDD10;
    min-width: 140px;
    position: relative;
    text-align: center;
    padding: 5px 0px;
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease; }
    .main-menu li.nav-item:after {
      position: absolute;
      right: 0px;
      top: 17%;
      content: '';
      width: 1px;
      height: 65%;
      background: #d8d8d8; }
    .main-menu li.nav-item:before {
      position: absolute;
      bottom: 0px;
      left: 5%;
      content: '';
      width: 0%;
      height: 4px;
      background: transparent; }
    .main-menu li.nav-item.active:before {
      background: #FBDD10;
      width: 90%; }
    .main-menu li.nav-item:last-child:after {
      background: transparent; }
    .main-menu li.nav-item:hover:before {
      background: #FBDD10;
      width: 90%;
      -webkit-transition: all 0.4s ease;
      -o-transition: all 0.4s ease;
      transition: all 0.4s ease; }
    .main-menu li.nav-item .nav-link {
      display: block;
      color: #FBDD10; }



