* {
  font-family: Montserrat-Light; }

* {
  color: #f6f6f6; }

body {
  background-color: #1e2124; }

.loading-content {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999999 !important; }
  .loading-content .loading-center {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .loading-content .sk-double-bounce {
    width: 100px;
    height: 100px;
    position: relative;
    margin: auto; }
  .loading-content .sk-double-bounce .sk-child {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #337ab7;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-animation: sk-double-bounce 2.0s infinite ease-in-out;
    animation: sk-double-bounce 2.0s infinite ease-in-out; }
  .loading-content .sk-double-bounce .sk-double-bounce-2 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s; }

@-webkit-keyframes sk-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0); }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes sk-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0); }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

.header {
  background: #000;
  border-bottom: 1px solid #1e2124; }
  .header__logo {
    width: 130px; }
  .header .navbar-nav .nav-link {
    color: #FBDD10; }
  .header .navbar-nav .nav-link-lang {
    color: #f5f5f5; }



