.quiz-loading {
  padding: 100px 0px;
  text-align: center; }
  .quiz-loading .loading {
    width: 1000px;
    height: 40px;
    position: relative;
    margin: auto; }
    .quiz-loading .loading .loader {
      position: absolute;
      left: 45%;
      top: 90%;
      -webkit-transform: translate(-45%, -90%);
          -ms-transform: translate(-45%, -90%);
              transform: translate(-45%, -90%); }
      .quiz-loading .loading .loader .Bar {
        background: #f8dc17;
        height: 0;
        position: absolute;
        bottom: 0;
        width: 10px;
        border-radius: 2px; }
        .quiz-loading .loading .loader .Bar:nth-child(0) {
          left: -15px;
          -webkit-animation: bottom-top 2s infinite ease-in-out 0s;
                  animation: bottom-top 2s infinite ease-in-out 0s; }
        .quiz-loading .loading .loader .Bar:nth-child(1) {
          left: 0px;
          -webkit-animation: bottom-top 2s infinite ease-in-out 0.2s;
                  animation: bottom-top 2s infinite ease-in-out 0.2s; }
        .quiz-loading .loading .loader .Bar:nth-child(2) {
          left: 15px;
          -webkit-animation: bottom-top 2s infinite ease-in-out 0.4s;
                  animation: bottom-top 2s infinite ease-in-out 0.4s; }
        .quiz-loading .loading .loader .Bar:nth-child(3) {
          left: 30px;
          -webkit-animation: bottom-top 2s infinite ease-in-out 0.6s;
                  animation: bottom-top 2s infinite ease-in-out 0.6s; }
        .quiz-loading .loading .loader .Bar:nth-child(4) {
          left: 45px;
          -webkit-animation: bottom-top 2s infinite ease-in-out 0.8s;
                  animation: bottom-top 2s infinite ease-in-out 0.8s; }
        .quiz-loading .loading .loader .Bar:nth-child(5) {
          left: 60px;
          -webkit-animation: bottom-top 2s infinite ease-in-out 1s;
                  animation: bottom-top 2s infinite ease-in-out 1s; }
        .quiz-loading .loading .loader .Bar:nth-child(6) {
          left: 75px;
          -webkit-animation: bottom-top 2s infinite ease-in-out 1.2s;
                  animation: bottom-top 2s infinite ease-in-out 1.2s; }
  .quiz-loading p {
    margin-top: 30px; }

@-webkit-keyframes bottom-top {
  0% {
    height: 0;
    opacity: 0; }
  50% {
    height: 30px;
    opacity: 1; }
  100% {
    height: 0;
    opacity: 0; } }

@keyframes bottom-top {
  0% {
    height: 0;
    opacity: 0; }
  50% {
    height: 30px;
    opacity: 1; }
  100% {
    height: 0;
    opacity: 0; } }



